@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  box-sizing: border-box;
}
.canvas {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background: whitesmoke;
}
.wrapper {
  width: 100%;
  height: 100%;
  min-height: 90vh;
  margin: 0 auto;
  position: relative;
}
.bg1 {
  width: 17%;
  height: auto;
  position: fixed;
  top: 83%;
  left: 95%;
  transform: translate(-83%, -93%);
  -webkit-filter: drop-shadow(4px 4px 5px black);
          filter: drop-shadow(4px 4px 5px black);
}
.main-footer {
  display: flex;
  width: 100%;
  height: 10vh;
  background-color: #560e03e3;
  color: white;
  align-items: center;
}
.main-footer h3 {
  font-family: "Montserrat", sans-serif;
  margin: 10px auto;
  font-size: 20px;
  color: #e8e2e2fa;
}

@media screen and (max-width: 768px) {
  .main-footer h3 {
    font-size: 16px;
  }
}
@media screen and (max-width: 524px) {
  .main-footer h3 {
    font-size: 14px;
  }
}

.nav-bar {
  position: -webkit-sticky;
  position: sticky;
  display: flex;
  top: 0px;
  padding: 0% 1%;
  width: 100%;
  height: 100px;
  margin: 0px auto;
  background-color: #a0301e;
  z-index: 10;
}

.nav-logo {
  display: flex;
  float: left;
  width: 30%;
  height: auto;
}
.my_logo {
  font-family: "Architects Daughter", cursive;
  color: white;
  font-size: 60px;
  margin: 7px 9px;
  color: #e6f1f3;
}
.nav-links {
  width: 46%;
  float: right;
  padding: 10px 15px;
  margin: 16px 0px;
  margin-left: auto;
}
.nav-links ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.nav-links ul li {
  font-size: 25px;
  list-style: none;
  padding: 5px 15px;
}
.nav-links ul a {
  color: #e6f1f3;
  font-family: "Montserrat", sans-serif;
}
.nav-links ul li::after {
  content: "";
  width: 0;
  height: 2px;
  background: white;
  display: block;
  margin: auto;
  transition: 0.5s;
}
.nav-links ul li:hover::after {
  width: 100%;
  cursor: pointer;
}
@media screen and (max-width: 768px) {
  .my_logo {
    font-size: 42px;
    margin: 21px 5px;
  }
  .nav-links {
    width: 55%;
    padding: 10px 5px;
    display: inline-block;
  }
  .nav-links ul li {
    font-size: 20px;
    padding: 14px 4px;
    margin: 0px 9%;
  }
}
@media screen and (max-width: 524px) {
  .nav-bar {
    height: 77px;
  }
  .my_logo {
    font-size: 24px;
    margin: 21px 1px;
  }
  .nav-links ul li {
    font-size: 13px;
    padding: 5px 2px;
    margin: 0px 5%;
  }
}

.title_room {
  text-align: center;
}
.title_room h1 {
  font-family: "Montserrat", sans-serif;
  margin: 10px 40px;
  font-size: 46px;
  color: #000000fa;
}
.title_room h2 {
  font-family: "Montserrat", sans-serif;
  margin: 10px 40px;
  font-size: 24px;
  color: #000000fa;
}

.title_room h1 span {
  color: #c12911;
  text-shadow: 3px 2px 2px black;
  font-size: 46px;
}
.title_room h2 span {
  color: #182628;
}
.search-panel {
  position: relative;
  height: 170px;
}
.search-box {
  position: absolute;
  width: 360px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #0a0a0a;
  height: 60px;
  border-radius: 40px;
  padding: 7px;
}
.search-input {
  border: none;
  background: none;
  outline: none;
  float: left;
  color: white;
  font-family: "Oxygen", sans-serif;
  font-size: 16px;
  line-height: 46px;
  width: 290px;
  padding: 0 10px;
}
.search-btn {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  color: black;
  background: #a0301e;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
}
.blank-error {
  position: absolute;
  top: 87%;
  left: 50%;
  transform: translate(-50%, -87%);
  font-size: 14px;
  color: #ea0101;
}

@media screen and (max-width: 768px) {
  .title_room h1 {
    font-size: 28px;
  }
  .title_room h1 span {
    font-size: 36px;
  }
  .search-box {
    width: 310px;
    height: 54px;
    padding: 5px;
  }
  .search-input {
    width: 240px;
    line-height: 42px;
  }
  .search-btn {
    width: 45px;
    height: 45px;
  }
}
@media screen and (max-width: 524px) {
  .title_room h1 {
    font-size: 24px;
  }
  .title_room h1 span {
    font-size: 24px;
    text-shadow: 2px 1px 2px black;
  }
  .title_room h2 {
    margin: 20px 40px 3px;
    font-size: 17px;
  }
  .search-box {
    height: 48px;
    padding: 4px;
    width: 240px;
  }
  .search-btn {
    width: 40px;
    height: 40px;
  }
  .search-panel {
    height: 120px;
  }
  .search-input {
    width: 184px;
    font-size: 12px;
    line-height: 40px;
  }
  .blank-error {
    margin-top: 10px;
  }
}

.results .clearfix {
  width: 50%;
  background-color: #ccbbbb;
  box-shadow: 5px 5px 12px black;
  font-family: "Oxygen", sans-serif;
  color: #3d3a3afc;
  overflow: auto;
  margin: 26px auto;
  display: block;
  transition: 0.2s ease-in-out;
  z-index: 2;
}
.results .clearfix:hover {
  background-color: #867e7e;
}
.results {
  margin-bottom: 100px;
}
section img {
  width: 150px;
  height: 210px;
  float: left;
  margin: 10px;
  border: 2px solid #f14922;
}
section img:hover {
  cursor: pointer;
}

.r-details {
  display: block;
  text-align: center;
  padding: 6% 8px;
  margin: 8px auto;
  cursor: pointer;
}

.r-details h2 {
  font-family: "Montserrat", sans-serif;
  color: #b9260e;
  font-size: 20px;
  font-weight: 600;
}
.r-details h3 {
  font-family: "Montserrat", sans-serif;
  margin-top: 20px;
}
.error {
  display: flex;
  justify-content: space-around;
}
.error span {
  font-family: "Montserrat", sans-serif;
  color: #182628;
  font-size: 22px;
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  .results .clearfix {
    display: flex;
  }
  section img {
    width: 36%;
    height: auto;
    margin: 3%;
  }
  .r-details {
    padding: 5% 6px;
  }
  .r-details h2 {
    font-size: 14px;
  }
  .r-details h3 {
    margin: 9% auto;
    font-size: 13px;
  }
  .error span {
    font-size: 20px;
  }
}

@media screen and (max-width: 524px) {
  .results .clearfix {
    width: 57%;
  }
  .error span {
    font-size: 16px;
  }
  .r-details h2 {
    font-size: 12px;
  }
  .r-details h3 {
    margin: 6% auto;
    font-size: 11px;
  }
}

#info_container {
  width: 50%;
  margin: 10px auto;
  display: block;
  position: relative;
}
.movie_img {
  width: 85%;
  height: auto;
  display: block;
  margin: 0px auto;
  border: 2px solid #f14922;
}
.basic_info {
  width: 67%;
  display: block;
  float: left;
  font-family: "Montserrat", sans-serif;
  color: #1f1d1dfc;
  margin: auto 7px;
  height: 64vh;
  overflow: auto;
}
.basic_info::-webkit-scrollbar {
  border-radius: 10px;
  width: 8px;
  /* background-color: grey; */
}
.basic_info::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #907a7a;
}
.basic_info::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #82281af5;
}
p {
  font-size: 14px;
  text-align: center;
  margin: 6px auto;
}
div .clearfx {
  overflow: auto;
  margin: 20px auto;
}
.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(61, 58, 58, 0.75);
}
.Modal {
  position: absolute;
  width: 65%;
  height: 75vh;
  top: 58px;
  left: 40px;
  right: 40px;
  bottom: 0px;
  background-color: #ccbbbb;
  box-shadow: 10px 10px 10px black;
  border-radius: 4px;
  outline: none;
  padding: 10px;
  margin: 60px auto;
  -webkit-animation: enter 700ms;
          animation: enter 700ms;
}
@-webkit-keyframes enter {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes enter {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
.movie-details span {
  text-align: left;
  font-size: 16px;
  color: #58170c;
}
.img-ratings {
  display: block;
  float: left;
  width: 30%;
  height: auto;
  color: #1f1d1dfc;
  padding: 3px;
}
.close-btn {
  width: 140px;
  height: 40px;
  border-radius: 35px;
  background-color: #c12911;
  color: #ded5d3;
  font-size: 14px;
  font-weight: bold;
  font-family: "Montserrat", sans-serif;
  display: flex;
  margin: 5px auto;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.close-btn:hover {
  background-color: #504a4a;
  color: white;
}
.info-footer {
  position: absolute;
  bottom: 0px;
  width: 98%;
  height: 50px;
}
.ratings {
  overflow: auto;
  max-height: 27vh;
}
.ratings::-webkit-scrollbar {
  border-radius: 10px;
  width: 5px;
  height: 5px;
}
.ratings::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #907a7a;
}
.ratings::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #82281af5;
}
@media screen and (max-width: 768px) {
  .Modal {
    margin: 48px auto;
  }
  .img-ratings {
    width: 100%;
    display: inline-flex;
    padding: 0px 8% 0px 5%;
  }
  .ratings {
    margin: 0px 3%;
  }
  .ratings p {
    margin: 24px 0px;
    font-size: 12px;
  }
  .movie_img {
    width: 115px;
    height: 155px;
  }
  .movie-details span {
    font-size: 14px;
  }
  .basic_info {
    width: 100%;
    padding: 5px;
    margin: 5px auto;
    height: 39vh;
  }
  .basic_info p {
    font-size: 12px;
  }
  .close-btn {
    width: 100px;
    height: 30px;
    border-radius: 28px;
    font-size: 12px;
  }
}
@media screen and (max-width: 524px) {
  .Modal {
    width: 78%;
  }
  .img-ratings {
    padding: 0px 3% 0px 3%;
  }
  .ratings p {
    margin: 12% 0px;
  }
  .movie-details span {
    font-size: 13px;
  }
  .basic_info p {
    font-size: 11px;
  }
  .basic_info::-webkit-scrollbar {
    width: 6px;
  }
}

